.App {
  margin-left: 10vw;
  margin-right: 10vw;
  margin-top: 10vh;
  margin-bottom: 10vh;
  width: 80%;
  display: inline-block;
}

.home .feature .item img {
  opacity: .5;
  width: 85%;
  margin-left: auto;
  margin-right: auto;
  /* filter: brightness(50%); */
}

.home .feature .item .item-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-weight: bold;
  font-size: 2em;
  text-align: center;
  margin-bottom: 10vh;
  color: #523f3f;
  line-height: 1em;
}
