.navgroup {
  float: right;
  display: flex;
  justify-content: center;
  margin-left: auto;
  margin-top: auto;
  margin-bottom: auto;
  align-items: center;
}

  
.title {
  float: left;
  text-align: left;
  text-decoration: none;
  display: flex;
  justify-content: center;
  margin-top: auto;
  margin-bottom: auto;
  margin-left: 10px;
  font-family: 'Reem Kufi', sans-serif;
  font-size: 5vw;
}

.current-tab {
  text-decoration: underline;
}

a {
  text-decoration: none;
  color: #ef938f;
}

.header {
  display: flex;
  width: 100%;
  overflow: visible;
  margin-bottom: 50px;
  font-family: 'Reem Kufi', sans-serif;
}

.header img {
  height: 5vw;
  width: 4.5vw;
  min-height: 50px;
  min-width: 45px;
}

nav a {
  padding: 10px;
  display: inline-block;
}

.navgroup nav {
  text-align: center;
}