.recipes img {
  object-fit: cover;
  width:100%;
  height:12vw;
}

.recipes {
  line-height: 2em;
}


/* recipe page */

.recipepage {
  padding: 10px;
  display: inline-block;
  height: 100%;
  width: 100%;
  line-height: 1.5em;
}

.recipepage .recipetitle {
  font-size: 2.5em;
  font-weight: bold;
  margin-bottom: 20px;
  margin-top: 50px;
  line-height: 1.2em;
  text-transform: capitalize;   
}

.recipepage .recipeingredients {
  margin-top: 50px;
}

.center{
  text-align: center;
}

.recipepage .recipeinfo{
  text-align: center;
}

.recipepage .recipebody {
  display: inline-block;
  width: 100%;
}

.recipepage .recipeimg {
  float: left;
  width: 50%;
  margin: 20px;
}
.recipetext {
  margin: 20px;
  /* padding: 10px */
}

.recipetext-right {
  padding-left: 50px !important;
}